<template>
<el-container>
  <el-header>
    <div class="header_logo">
        <img src="../../static/img/header_logo1.png">
    </div>
  </el-header>
  <el-main>
    <div class="con">
      <div class="row mode01">
      <div class="contentLf">
         <el-carousel height="400px">
           <el-carousel-item v-for="(index, item) in robot" :key="item">
             <img class="cobot" :src="index" :title="index.split('/')[3].split('.')[0]"/>
           </el-carousel-item>
         </el-carousel>
      </div>
      <div class="contentRt">
        <div class="form-signin" id="login" name="login">
          <div class="signin-heading">
            <a>服务质量评价系统</a>
          </div>
          <el-form ref="form" :model="form" label-width="20%" style="margin-top: 40px;margin-left: 25px; width:300px">
          <el-form-item label="用户名:">
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="密  码:">
            <el-input v-model="form.password" type="password"></el-input>
          </el-form-item>
        </el-form>
          <div class="reg">
            <el-button type="primary" @click="login" class="btn">登录</el-button>
            <span @click="register">还没有账户？ 前往注册</span>
          </div>
        </div>
      </div>
    </div>
      <div class="row mode02">
        <ul style="list-style-type:none">
          <li>
            <div class="lk">
              <h3>多类型服务测试</h3>
              <div class="lk_con">
                支持对网页、流媒体、即时通讯等众多类型网络应用服务的质量测试，可以进行准确客观的服务质量评估
              </div>
            </div>
          </li>
          <li>
            <div class="lk">
              <h3>更细粒度评估</h3>
              <div class="lk_con">
                突破探测指标单一、模型少难题，可实现不同服务的针对性细粒度服务质量评估
              </div>
            </div>
          </li>
          <li>
            <div class="lk">
              <h3>多维度服务质量评价</h3>
              <div class="lk_con">
               突破网络指标与量化评估映射技术难题，可构建层次化、多等级服务质量评估模板
              </div>
            </div>
          </li>
          <li>
            <div class="lk">
              <h3>更灵活模板配置</h3>
              <div class="lk_con">
               突破用户需求多样性难题，可实现“服务-探测指标-评分模板”的多级映射评估灵活配置。
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </el-main>
  <el-footer>技术支持：XXXXXXXXXXX   联系电话：xxxxxxxxx</el-footer>
</el-container>
</template>
<script>
import {loginapi} from '../api/api.js'
export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      robot: [
        '../static/img/lunbo1.jpg',
        '../static/img/lunbo2.jpg',
        '../static/img/lunbo3.jpg',
        '../static/img/lunbo4.jpg'
      ]
    }
  },
  methods: {
    login () {
      if (this.form.username === '') {
        this.$message.error('用户名不能为空')
      } else if (this.form.password === '') {
        this.$message.error('密码不能为空')
      } else {
        loginapi(this.form).then(res => {
          if (res.status === 200) {
            // 利用localstorage存储到本地
            localStorage.setItem('token', res.data.token)
            this.$router.push({
              path: '/home',
              query: {
                name: this.form.username
              }
            })
          } else {}
        }).catch(err => {
          this.$alert('用户名或密码错误', '登录失败', {
            confirmButtonText: '确定',
            callback: action => {
              this.form.username = ''
              this.form.password = ''
            }
          })
          console.log('登录失败 ' + err)
        })
      }
    },
    register () {
      this.$router.push('/register')
    }
  }
}
</script>

<style scoped>
  .el-container{
    width: 100vw;
    height: 100vh;
  }
  .el-header {
    background: #f5f5f5;
    color: #333;
    text-align: center;
    line-height: 60px;
    height: calc(12vh) ! important;
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid #7aa8d0;
  }
  .header_logo{
    margin-top: auto;
    margin-right: 20%;
    margin-bottom: auto;
    margin-left: 22%;
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
  }
  .header_logo img{
    width: 72%;
    height: 80%;
    overflow: hidden;
  }
  .el-main {
    background-color: #ffffff;
    color: #333;
    text-align: center;
    line-height: 160px;
    width: 100%;
  }
  .con{
    max-width: 1200px;
    height: auto;
    margin: 0 auto;
    overflow: hidden;
    padding: 30px 0 0px 0;
  }
  .mode01{
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
  }
  .contentLf{
    width: 61%;
    float: left;
    height: 380px;
    overflow: hidden;

  }
  @media screen and (max-width: 1200px) {
    .contentLf{display: none}
    .el-header {
      width: 1300px
    }
  }
  .contentRt{
    background: #f1f1f1;
    width: 380px;
    height: 380px;
    display: inline-block;
    border-radius: 5px;
    border: 1px solid #e6e6e6;
    border-top: 0px;

  }
  #login {
     margin: 0 auto;
     height: 100%;
   }
  .signin-heading {
    text-align: left;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    height: 80px;
    line-height: 60px;
    background: #005bac;
    font-size: 18px;
    color: #525252;
    font-weight: normal;
  }
  .signin-heading a {
    line-height: 80px;
    font-size: 22px;
    color: #fff;
    padding: 4px 4px 0 30px;
    display: block;
    width: auto;
    float: left;
  }
  .el-footer {
    background: #f2f2f2;
    color: #333;
    text-align: center;
    line-height: 60px;
    height:20% ! important;
    width: 100%;
    margin-left: 0;
    overflow: hidden;
    border-top: 1px solid #79a6cf;

    }
  .btn {
    display: block;
    float: left;
    font-weight: normal;
    font-size: large;
    font-family: "Microsoft yahei";
    margin: 0 0 0 50px;
    background-color: #005bac;
    width: 120px;
    height: 40px;
    line-height: 10px;
    border-color: #005bac;
  }
  .reg{
    margin-top: 50px;
    height: 40px;
  }
  .reg span{
    font-size: 15px;
    float: left;
    margin: 0px 0 0 35px;
    line-height: 40px;
  }
  .mode02{
    margin: 60px auto 60px;
  }
  .mode02 ul{
    overflow: hidden;
  }
  .mode02 ul li {
    width: 25%;
    float: left;
  }
  .lk {
    padding: 0 45px 0 55px;
    background:  no-repeat 20px top;

  }
  .lk h3 {
    font-size: 20px;
    line-height: 20px;
  }
  .lk_con {
    font-size: 12px;
    color: #666666;
    margin: 20px 0 0 0;
    line-height: 22px;
    height: 70px;
    overflow: hidden;
  }
</style>



